import { getWxConfig } from '@/libs/utils'
import wx from 'wx'
export default {
  methods: {
    async share () {
      const _this = this
      getWxConfig().then(() => {
        _this.wxJsSdkConfig()
      }).catch((e) => {
        console.log(e)
        return _this.Toast('')
      })
    },
    wxJsSdkConfig () {
      // 
      const _this = this
      console.log(_this.title)
      console.log(_this.url)
      console.log(_this.imgUrl)
      wx.onMenuShareTimeline({
        title: _this.title, // 
        link: _this.url, // JS
        imgUrl: _this.imgUrl, // 
        success: () => {
          console.log('')
          _this.$emit('share-success')
        },
        cancel: () => {
          // 
        }
      })
      // 
      wx.onMenuShareAppMessage({
        title: _this.title, // 
        desc: _this.desc, // 
        link: _this.url, // JS
        imgUrl: _this.imgUrl, // 
        type: '', // ,musicvideolinklink
        dataUrl: '', // typemusicvideo
        success: () => {
          _this.$emit('share-success')
        },
        cancel: () => {
          // 
        }
      })
      // QQ
      wx.onMenuShareQQ({
        title: _this.title, // 
        desc: _this.desc, // 
        link: _this.url, // 
        imgUrl: _this.imgUrl, // 
        success: () => {
          _this.$emit('share-success')
        },
        cancel: () => {
          // 
        }
      })
      // QQ
      wx.onMenuShareQZone({
        title: _this.title, // 
        desc: _this.desc, // 
        link: _this.url, // 
        imgUrl: _this.imgUrl, // 
        success: () => {
          _this.$emit('share-success')
        },
        cancel: () => {
          // 
        }
      })
    }
  }
}
